li {
  flex-direction: column;
  width: 20%;
  display: flex;
}

.movie-list {
  flex-wrap: wrap;
  justify-content: center;
  gap: 40px 20px;
  width: 1500px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.movie-list img {
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  width: 100%;
  height: auto;
}

.movie-info {
  background-color: #f4f4f4;
  height: 80px;
  padding: 15px;
  box-shadow: 0 4px 8px #0003;
}

.movie-info h2 {
  color: #333;
  margin: 0;
  font-size: 20px;
}

.movie-info p {
  color: #777;
  margin: 5px 0;
  font-size: 12px;
}

.button-container {
  justify-content: center;
  align-items: center;
  margin-top: 30px;
  display: flex;
}

.load-more.load-more-hidden {
  display: none;
}

.load-more {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 1.2em;
  font-weight: bold;
  transition: background-color .3s ease-in-out, transform .2s ease-in-out;
  display: inline-block;
}

.load-more:hover {
  background-color: #0056b3;
  transform: scale(1.05);
}

